@font-face{font-family:"MTAVRULI";src:url("./assets//fonts/bpg_extrasquare_mtavruli_2009.ttf")}@font-face{font-family:"Noto Sans Georgian";src:url("./assets/fonts/NotoSansGeorgian-Medium.ttf")}:root{--font-primary-ge: $font-primary-ge;--font-primary-en: $font-primary-en}@media screen and (max-width: 490px){.navBar{padding:15px 0px !important;gap:0px !important}}.nav_wrapper{width:100%}.nav-side li{padding:0px}.nav-side li a{width:100%;height:100%;text-decoration:none;color:#00235b;padding:20px}.nav-side li:hover{background-color:#eeeded}.navBar{width:100%;display:flex;align-items:center;justify-content:space-between;padding:15px 50px;gap:20px}.navBar .brand a:hover{border:none}.navBar .brand a{display:flex;gap:20px;align-items:center;text-decoration:none}.navBar .sign_acc{display:none}.navBar .MuiIconButton-colorInherit{margin:0 !important}@media screen and (max-width: 680px){.navBar .brand span{display:none}.navBar .sign_up{gap:0px}.navBar .sign_up .sign_btn{display:none !important}.navBar .sign_up .sign_acc{display:block}.navBar section{gap:8px !important}}.navBar .poor_sight{display:flex;gap:10px;align-items:center;border-radius:15px;border:none;background-color:#eeeded;padding:0 8px}.navBar .poor_sight i{color:#00235b}.navBar .poor_sight span{color:#00235b}@media screen and (max-width: 1100px){.navBar .poor_sight{display:flex;justify-content:center;align-items:center;border-radius:50px;height:30px;width:30px}.navBar .poor_sight span{display:none}}.navBar .search{display:flex;position:relative}.navBar .search .search-icon{position:absolute;top:50%;right:10px;transform:translateY(-50%);font-size:18px;color:#999;pointer-events:none}.navBar input{border:none;padding-right:30px;width:250px;background-color:#eeeded;border-radius:20px}.navBar .search::after{font-family:"FontAwesome";content:"";display:none}@media screen and (max-width: 780px){.navBar input{width:150px}}@media screen and (max-width: 620px){.navBar input{display:none}.navBar .search-icon{display:none}.navBar .search::after{display:block}}@media screen and (max-width: 500px){.navBar .brand{padding:0 !important}.navBar .profile a p{display:none}.navBar .profile .wrap::after{display:block !important}}.navBar section{display:flex;justify-content:flex-end;gap:40px;align-items:center}.navBar section .lang_change{height:35px}.navBar section .sign_up{gap:15px}.navBar section .sign_up button{padding:6px 10px;border-radius:5px;background-color:#00235b;color:#fff;font-weight:bolder;border:none}.navBar section .profile a{color:#eeeded}.navBar section .profile a p{margin:0 !important}.navBar section .profile .wrap::after{content:"";font-family:"FontAwesome";display:none}.accord{border:none}.sign_in_modal{display:flex;align-items:center;justify-content:center;z-index:1300}.modal-dialog{z-index:1300}.modal-backdrop{z-index:1300}@media screen and (max-width: 1250px){.nav{display:none}}.nav-link{height:100%;display:flex;align-items:center;justify-content:center;color:#00235b !important;border:none !important}.nav-link a{width:100%;height:100%}.nav-tabs{border:none !important}.nav-tabs .active{background-color:#ebedff !important;color:#000 !important}