nt:center !important;flex-direction:column;align-items:center !important;padding:20px 0px !important;height:auto !important}.bottom_footer .socials div{background-color:unset !important;justify-content:center !important;gap:10px}.bottom_footer div{width:80%;justify-content:center;align-items:center}}@media screen and (max-width: 650px){.upper_footer{flex-direction:column}.upper_footer .brand{padding:0 !important;display:flex;justify-content:center}.upper_footer .links,.upper_footer .contact{display:flex;flex-direction:column;align-items:center !important;gap:15px}.upper_footer .links h3,.upper_footer .contact h3{text-align:center}.upper_footer .links ul,.upper_footer .contact ul{width:100%;padding-left:40px !important}.upper_footer .links ul li,.upper_footer .contact ul li{text-align:center}.upper_footer div{padding:50px}.upper_footer div a{display:flex;justify-content:flex-start}.upper_footer div a div{padding:0 !important}}@media screen and (max-width: 900px){.brand{padding-left:25px !important}}@media screen and (max-width: 700px){.brand{padding-left:25px !important}}