play:flex;align-items:center;justify-content:center;background-color:#fff}.mainPage_wrapper .vacancy_hero_col .hero_box img{width:45%;border-radius:10px}.mainPage_wrapper .vacancy_hero_col p{width:100%;margin:0 !important;padding:0 !important;object-fit:cover}.mainPage_wrapper .vacancy_hero_col a{width:70%}.mainPage_wrapper .vacancy_hero_col button{background-color:#00235b;color:#eeeded;border:none;border-radius:5px;padding:5px 10px;width:100%}.row{margin-right:0px !important}@media screen and (max-width: 1220px){.mainPage_wrapper{flex-direction:column;gap:10px}.mainPage_wrapper .swiper_col{width:50%}.mainPage_wrapper .swiper_col .news-swiper{width:100%;height:100%}.mainPage_wrapper .swiper_col .news-swiper .swiper-wrapper{height:100%}.mainPage_wrapper .swiper_col .news-swiper .swiper-wrapper .swiper-slide{height:auto}.mainPage_wrapper .vacancy_hero_col{width:50%}}@media screen and (max-width: 800px){.mainPage_wrapper{gap:50px !important}.mainPage_wrapper .hero_box{flex-direction:column;gap:15px;align-items:center}.mainPage_wrapper .hero_box div{width:100% !important;align-items:center}.mainPage_wrapper .hero_box p{width:100%;text-align:center}.mainPage_wrapper .hero_box h3{width:80%;text-align:center}.mainPage_wrapper .hero_box img{width:50%}.swiper_col{width:90% !important;margin:0 !important;padding:0 !important}.swiper_col .swiper-slide{height:300px !important}.vacancy_hero_col{width:60vw !important;padding:0 !important;margin:0 !important}.row{flex-direction:column;align-items:center;gap:20px;margin:0 0 20px 0 !important}}@media screen and (max-width: 400px){.vacancy_hero_col{width:90vw !important}}@media screen and (min-width: 2200px){.swiper-slide{height:350px !important}}